<template>
	<div>
		<div class="back-title">
			<span>消息记录</span>
		</div>
		<div class="pagebox">
			<Menu mode="horizontal" active-name="1" @on-select="turnUrl" class="item-title-box">
		        <MenuItem name="/message/notice">
		            	通知
		        </MenuItem>
		        <MenuItem name="/message/subscribe">
		            	关注
		        </MenuItem>
		        <MenuItem name="/message/comment">
		            	评论
		        </MenuItem>
		    </Menu>
		    <router-view></router-view>
		</div>
    </div>
</template>
<script>
 export default {
   methods: {
     turnUrl (name) {
       this.$router.push(name)
     }
   }
 }
</script>


